import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import Typing from 'react-typing-animation';
import Delay from 'react-delay';

import { SCREEN_ORIENTATION_PORTRAIT } from 'constants/screen';
import bgImg from 'images/bg2.jpg';
import astroImg from 'images/astro.png';
import tvImg from 'images/tv.png';

const scaleAnimation = keyframes`
  from, 10%, to {
    background-position: center top;
  }
  50%, 60% {
    background-position: center bottom;
  }
`;

const astroAnimation = keyframes`
  from, to {
    background-position: center top;
  }
  50% {
    background-position: center -50%;
  }
`;

const tvAnimation = keyframes`
  from, to {
    top: 62%;
  }
  50% {
    top: 67%;
  }
`;

const bigButtonGradientAnimation = keyframes`
  from {
    background-position: 0% 0%
  }
  to {
    background-position: 100% 0%
  }
`;

const overlayHideAnimation = keyframes`
  from {
    background-color: black;
  }
  to {
    background-color: transparent;
  }
`;

const showUpAnimation = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`;

const Overlay = styled.div`
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  animation: ${overlayHideAnimation} 1s ease-in-out;
`;

const LeftHidding = styled.div`
  position: absolute;
  left: 0;
  right: 50%;
  top: 0;
  bottom: 0;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.74) -8.29%, rgba(64, 64, 64, 0) 100%);
`;

const RightHighlighting = styled.div`
  position: absolute;
  left: 60%;
  right: 0;
  top: 0;
  bottom: 0;
  background: linear-gradient(90deg, rgba(234, 235, 185, 0.11) -8.29%, rgba(212, 212, 212, 0) 100%);
  transform: rotate(180deg);
  z-index: 100;
`;

const Container = styled.div`
  width: 100%;
  height: 100%;
  margin-left: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? '0' : '50px')};
  margin-top: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? '50px' : '0')};
  background-position: center;
  background-repeat: no-repeat;
  background-size: 105%;
  background-image: url(${bgImg});
  animation: ${scaleAnimation} 30s ease-in-out infinite;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
`;

const Astro = styled.div`
  width: 40%;
  background-position: center top;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url(${astroImg});
  animation: ${astroAnimation} 6s ease-in-out infinite;
  position: absolute;
  left: 13%;
  top: 0;
  bottom: 0;
  z-index: 105;
`;

const TVContainer = styled.div`
  width: 150px;
  height: 150px;
  animation: ${tvAnimation} 10s ease-in-out infinite;
  position: absolute;
  left: 48%;
  right: 0;
  top: 66%;
  z-index: 104;
`;

const TVImage = styled.div`
  width: 100%;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url(${tvImg});
  position: relative;
  z-index: 104;
`;

const TVCanvas = styled.div`
  width: 84px;
  height: 72px;
  border: 1px solid red;
  position: absolute;
  top: 38px;
  left: 34px;
  transform: rotate(-10deg);
  z-index: 103;
  background: white;
`;

const LabelsWrapper = styled.div`
  height: auto;
  margin: auto;
  margin-right: 20%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  z-index: 102;
`;

const BigLabel = styled.div`
  font-family: Proxima Nova Condensed;
  font-style: normal;
  font-weight: normal;
  line-height: 55px;
  font-size: 45px;
  color: #D1D1D1;
  background: rgba(0, 0, 0, 0.44);
  padding: 0px 16px;
  margin-top: ${({ marginTop }) => marginTop || 0}px;
`;

const BigButtonContainer = styled.div`
  text-align: right;
  width: 100%;
  height: 60px;
  display: block;
  margin-top: 53px;
`;

const BigButton = styled.button`
  padding: 5px 35px;
  background: linear-gradient(270deg, #45A19B 0%, #CEE866 0.01%, #C03F3F 54.75%, #EB5757 100%);
  background-size: 400% 100%;
  background-position: 0% 0%;
  border-radius: 48px;
  cursor: pointer;
  outline: none;
  border: none;
  font-family: Proxima Nova Condensed;
  font-style: normal;
  font-weight: normal;
  line-height: 43px;
  font-size: 35px;
  color: #FFFFFF;
  animation: ${showUpAnimation} 1s ease-in-out;
  transition: background 2s ease;

  &:hover {
    background-position: 100% 0%;
    transition: background 2s ease;
  }
`;

export default class MainPageContainer extends Component {
  render() {
    const { orientation } = this.props;
    const typingProps = {
      speed: 70,
      cursor: null
    };
    return (
      <Container orientation={orientation} bgImg={bgImg}>
        <LabelsWrapper>
          <Typing startDelay={1000} {...typingProps}>
            <BigLabel>Hello! I'm Airomad</BigLabel>
          </Typing>
          <Typing startDelay={3000} {...typingProps}>
            <BigLabel marginTop={24}>Im a Frontend Developer</BigLabel>
          </Typing>
          <Typing startDelay={5000} {...typingProps}>
            <BigLabel>from Kharkiv, Ukraine</BigLabel>
          </Typing>
          <BigButtonContainer>
            <Delay wait={7000}>
              <BigButton>Get in touch</BigButton>
            </Delay>
          </BigButtonContainer>
        </LabelsWrapper>
        <Astro />
        <TVContainer>
          <TVCanvas>
            <iframe style={{width: '100%', height: '100%'}} frameborder="0" allowfullscreen="" src="https://www.youtube.com/embed/-C_rvt0SwLE?autoplay=1" />
          </TVCanvas>
          <TVImage />
        </TVContainer>
        <Overlay />
        <LeftHidding />
        <RightHighlighting />
      </Container>
    );
  }
}



