import React, { Component } from 'react';
import styled from 'styled-components';

import { SCREEN_ORIENTATION_PORTRAIT } from 'constants/screen';
import logo from 'images/logo.svg';

const Container = styled.div`
  position: absolute;
  width: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? '100%' : '50px')};
  height: ${({ orientation }) => (orientation === SCREEN_ORIENTATION_PORTRAIT ? '50px' : '100%')};
  top: 0px;
  left: 0px;
  background: #1A1A1A;
  color: red;
  z-index: 200;
`;

const LogoContainer = styled.div`
  width: 50px;
  height: 50px;
  background: #070707;
  position: absolute;
  font-size: 25px;
  font-family: Proxima Nova Condensed;
  color: #FFFFFF;
  left: 0px;
  top: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 2s ease-out;
  &:hover {
    background: yellow;
    transition: background-color 2s ease-out;
  }
`;

const Logo = styled.div`
  margin: auto;
  width: 35px;
  height: 35px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(${logo});
`;

export default class NavBar extends Component {
  render() {
    const { URL, orientation } = this.props;
    return (
      <Container orientation={orientation}>
        <LogoContainer>
          A
        </LogoContainer>
        {URL}
      </Container>
    );
  }
}



