import { SCREEN_ORIENTATION_LANDSCAPE, ON_SCREEN_ORIENTATION_CHANGE } from 'constants/screen';

const INITIAL_STATE = {
  orientation: SCREEN_ORIENTATION_LANDSCAPE
};

export default function screen(state = INITIAL_STATE, action) {
  switch (action.type) {

    case ON_SCREEN_ORIENTATION_CHANGE:
      return {
        ...state,
        orientation: action.orientation
      };

    default:
      return state;
  }
}



